.video-entry {
    background-color: var(--background);
    padding: 15px 15px 15px 15px;
}

h2 {
    margin-top: 5px;
}

button {
    margin-top: 0px;
    border-radius: 0px;
    border-color: var(--mid);
    width: 100%;
    background-color: var(--light);
    color: var(--font);
    padding: 5px;
    font-size: larger;
}

.enabled {
    background-color: var(--accent);
}

.disabled {
    background-color: var(--mid);
}