* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /* font-family: ''*/
}

body {
    background-color: var(--light);
}

.navbar {
    z-index: 10;
    background-color: var(--background);
    /* height: 80px; */
    height: var(--navbar_height);
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.menu-bars {
    z-index: 10;
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    z-index: 10;
    background-color: var(--mid);
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0; 
    left: -100%; 
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms; 
}

.nav-text {
    z-index: 10;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 10px; 
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: var(--font);
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex; 
    align-items: center;
    padding: 0px 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: var(--background);
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: var(--background);
    width: 100%;
    height: 80px;
    display: flex;
    justify-items: start;
    align-items: center;
}

span {
    margin-left: 16px;
}