:root {
  /* Colours */
  --background: #1e2126;
  --mid: #282c36;
  --light: #2c313c;
  --font: #EDEDED;
  --logos: crimson;
  --accent: crimson;
  --stop: crimson;
  --start: green;

  /* Other Variables */
  --navbar_height: 80px;
}

[data-theme='dark'] {
  --background: #1e2126;
  --mid: #282c36;
  --light: #2c313c;
  --font: #EDEDED;
  --logos: crimson;
  --accent: crimson;
  --stop: crimson;
  --start: green;
}

[data-theme='light'] {
  --background: #6076a5;
  --mid: #7b8eb7;
  --light: #9eb2de;
  --font: #282828;
  --logos: #e16880;
  --accent: #e16880;
  --stop: #e16880;
  --start: rgb(110, 182, 110);
}

.App {
  background-color: var(--light);
  color: var(--font);
  margin: 25px;
  top: 0%;
  left: 0%;
}

body {
  margin: 0;
}

.Container-Centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(100vh - (var(--navbar_height) / 100 * vh)); 
}

.Webcam {
  border-style: solid;
  border-color: var(--background);
  border-width: 5px;
  border-radius: 25px;
}

button {
  color: var(--font);
  margin-top: 10px;
  padding: 10px 40px;
  border-style: solid;
  border-width: 3px;
  border-radius: 25px;
  cursor: pointer;
}

button.stop {
  border-color: var(--stop);
  background-color: rgba(0, 0, 0, 0);
}

button.start {
  border-color: var(--start);
  background-color: rgba(0, 0, 0, 0);
}

.text-box {
  background-color: var(--background);
  padding: 25px;
  color: var(--font);
  border-radius: 25px;
}
